/**
 * @author GuangHui
 * @description 基类
 */

export interface ISize {
  width: number
  height: number
}

export interface IPos {
  x: number
  y: number
}

export interface IRect extends ISize, IPos {}

export enum LayoutItemTypeEnum {
  ROW = 'ROW',
  IMG = 'IMG',
  GRAPH = 'GRAPH',
  GRAPH_TITLE = 'GRAPH_TITLE',
  IMG_TITLE = 'IMG_TITLE',
  CHAR = 'CHAR',
  FORMULA = 'FORMULA',
  IMG_PLACEHOLDER = 'IMG_PLACEHOLDER',
  TEXT_GROUP = 'TEXT_GROUP'
}

export interface IGroup {
  groups: Base[]
}

export interface IChild {
  childs: Base[]
}

export interface IContent {
  rawContent: string
  content: string
}

export interface IRow {
  rowNo: number
  indent: number
}

export enum HorizontalAlignEnum {
  LEFT = 'LEFT',
  RIGHT = 'RIGHT',
  MIDDLE = 'MIDDLE'
}

export interface IHorizontalAlign {
  horizontalAlign: HorizontalAlignEnum
}

export enum SurrounTypeEnum {
  NONE = 'NONE',
  FLOAT = 'FLOAT',
  ABSOLUTE = 'ABSOLUTE'
}

export interface ISurround {
  surroundType: SurrounTypeEnum
}

export abstract class Base implements IRect {
  x = -1
  y = -1
  width = -1
  height = -1

  abstract layoutItemType: LayoutItemTypeEnum
  abstract canLineBreak: boolean

  abstract getPos(): IPos | Promise<IPos>
  abstract getSize(): ISize | Promise<ISize>
}
